// import { useState } from 'react';
import { ErrorMessage, Formik } from 'formik';
// import { useDispatch, useSelector } from 'react-redux';
import { getColor } from 'utils/formikColors';
// import { getAvatar } from 'redux/auth/authSelectors';
// import { updateUserInfo } from 'redux/auth/authOperations';
import sprite from '../../../images/sprite.svg';
import * as Yup from 'yup';

import {
  UserForm,
  FlagInput,
  InputWrapper,
  NameInput,
  NameLabel,
  UserAvatarWrapper,
  UserIcon,
  UserSvgWrapper,
  BtnWrapper,
  SaveChangesBtn,
} from './HeaderUserForm.styled.js';

const SUPPORTED_FORMATS = ['image/jpg', 'image/jpeg', 'image/png'];

const EditNameSchema = Yup.object().shape({
  picture: Yup.mixed().test(
    'type',
    'Only PNG, JPEG and JPG formats are supported',
    value => {
      return !value || (value && SUPPORTED_FORMATS.includes(value?.type));
    }
  ),

  name: Yup.string()
    .min(1, 'Name must contain at least 2 letters')
    .max(16, 'Name must contain maximum 16 letters')
    .matches(
      /^[--1-9a-zA-Z]+(([' -][--1-9a-zA-Z])?[--1-9a-zA-Z]*)*$/,
      'Name must contain only letters'
    )

    .required('Name is required'),
});

export const UserFormAssembly = ({ name, avatarUrl, closeModal }) => {
  // const dispatch = useDispatch();
  // eslint-disable-next-line no-undef
  // const userAvatar = useSelector(getAvatar);
  // const [path, setPath] = useState(userAvatar);

  const handleSubmit = values => {
    const formData = new FormData();
    formData.append('name', values.name.trim());
    formData.append('picture', values.picture);
    // dispatch(updateUserInfo(formData));
    closeModal();
  };

  return (
    <Formik
      initialValues={{
        picture: '',
        name: name,
      }}
      validationSchema={EditNameSchema}
      onSubmit={(values, actions) => {
        handleSubmit(values);
        actions.setSubmitting(false);
        actions.resetForm();
      }}
    >
      {props => (
        <UserForm onSubmit={props.handleSubmit}>
          <UserAvatarWrapper>
            <label htmlFor="picture" id="labelFile">
              {/* {path ? ( */}
              {0 ? (
                <UserSvgWrapper>
                  {/* <img src={path} alt="user_picture" /> */}
                  <img src={0} alt="user_picture" />
                </UserSvgWrapper>
              ) : (
                <UserSvgWrapper>
                  <svg>
                    <use href={sprite + `#user`} />
                  </svg>
                </UserSvgWrapper>
              )}
            </label>
            <input
              type="file"
              id="picture"
              name="picture"
              onBlur={() => {
                props.setTouched({
                  picture: true,
                });
              }}
              onChange={event => {
                if (event.target.files[0]) {
                  if (SUPPORTED_FORMATS.includes(event.target.files[0].type)) {
                    // setPath(window.URL.createObjectURL(event.target.files[0]));
                    props.setFieldValue('picture', event.target.files[0]);
                  }
                  props.setFieldValue('picture', event.target.files[0]);
                }
              }}
            />
          </UserAvatarWrapper>
          {props.errors.picture && props.touched.picture ? (
            <ErrorMessage location="file">{props.errors.picture}</ErrorMessage>
          ) : null}

          <InputWrapper>
            <NameLabel htmlFor="name" id="labelName">
              <NameInput
                type="text"
                name="name"
                id="name"
                onBlur={() => {
                  props.setTouched({
                    name: true,
                  });
                }}
                onChange={event => {
                  props.setTouched({
                    name: true,
                  });
                  props.setFieldValue('name', event.target.value);
                }}
                color={getColor(
                  props.errors.name,
                  props.values.name,
                  '#C4C4C4'
                )}
              />
              <UserIcon
                stroke={getColor(
                  props.errors.name,
                  props.values.name,
                  '#C4C4C4'
                )}
              >
                <use href={sprite + `#user`} />
              </UserIcon>
              {props.touched.name && props.values.name ? (
                <FlagInput>
                  <use
                    href={`${sprite}${getColor(
                      props.errors.name,
                      props.values.name
                    )}`}
                  ></use>
                </FlagInput>
              ) : (
                props.values.name && (
                  <BtnWrapper
                    type="button"
                    onClick={() => props.setFieldValue('name', '')}
                  >
                    <svg>
                      <use href={sprite + `#edit`} />
                    </svg>
                  </BtnWrapper>
                )
              )}
            </NameLabel>
            {props.errors.name && props.touched.name ? (
              <ErrorMessage>{props.errors.name}</ErrorMessage>
            ) : null}
            <SaveChangesBtn
              type="submit"
              disabled={
                !(
                  (props.touched.name &&
                    props.values.name &&
                    !props.errors.name) ||
                  (props.touched.picture &&
                    props.values.picture &&
                    !props.errors.picture)
                )
              }
            >
              Save changes
            </SaveChangesBtn>
          </InputWrapper>
        </UserForm>
      )}
    </Formik>
  );
};
